.contacts {
  max-width: 992px;
  margin: 64px auto;

  @media (max-width: 990px) {
    width: 90%;
  }

  .title {

    p {
      color: var(--secondary, #159EEC);
      font-family: 'Work Sans', sans-serif;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      letter-spacing: 2.88px;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    h1 {
      color: #007E85;
      font-family: 'Yeseva One';
      font-size: 32px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  .contacts_block {
    margin-top: 64px;
    display: flex;
    gap: 20px;

    @media (max-width: 990px) {
      flex-wrap: wrap;
      justify-content: center;
    }

    .contacts_card {
      width: 233px;
      height: 233px;
      border-radius: 5px;
      background: var(--accent, #BFD2F8);
      padding: 50px 20px;
      font-family: ' Work Sans', sans-serif;
      transition: .5s;

      @media (max-width: 500px) {
        width: 165px;
        height: 100px;
        padding: 19px 15px;
      }

      .contacts_img {
        margin-bottom: 15px;

        @media (max-width: 500px) {
          display: none;
        }

        svg {
          path {
            transition: .5s;
          }
        }
      }

      p,
      a {
        transition: .5s;
      }

      p {
        color: #007E85;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 8px;

        @media (max-width: 500px) {
          font-size: 14px;
        }
      }

      a {
        color: #007E85;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        display: block;
        margin-bottom: 5px;

        @media (max-width: 500px) {
          font-size: 12px;
        }
      }

      &:hover {
        background: #007E85;

        p,
        a {
          color: var(--accent, #BFD2F8);
        }

        .contacts_img {
          svg {
            path {
              stroke: #BFD2F8;
            }
          }
        }
      }
    }
  }
}